.react-logo {
  width: 120px;
  height: 120px;
}

.react-logo__circle, .react-logo__ring {
  transition: ease-out 1.2s;
  transform-origin: center;
}

.react-logo__circle {
  transform: scale(1.3) translate(180px, 108px);
}

.react-logo__ring--1 {
  transition: ease-out 0.7s;
  transition-delay: 2s;
  transform: scale(0);
}

.react-logo__ring--2 {
  transition-delay: 0.7s;
  transform: rotateZ(60deg);
}

.react-logo__ring--3 {
  transition-delay: 1.2s;
  transform: rotateZ(-60deg);
}

body:hover .react-logo__circle {
  transform: scale(1) translate(0);
}

body:hover .react-logo__ring--1 {
  transform: scale(1);
}

body:hover .react-logo__ring--2, body:hover .react-logo__ring--3 {
  transform: rotateZ(0deg);
}

