img {
  animation: 0s ease-out 3s 1 animg;
}

@keyframes animg {
  0% {
    transform: translate3d(0px, 24px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    opacity: 0;
    transform-style: preserve-3d;
  }
  100% {
    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg, 0deg);
    opacity: 1;
    transform-style: preserve-3d;
  }
}

.show-on-scroll {
  opacity: 0;
  -webkit-transform: translateY(10em);
  transform: translateY(10em);
  -webkit-transition: opacity .3s .25s ease-out,
              -webkit-transform 4s .25s cubic-bezier(0,1,.3,1);
  transition: opacity .3s .25s ease-out,
              -webkit-transform 4s .25s cubic-bezier(0,1,.3,1);
  transition: transform 4s .25s cubic-bezier(0,1,.3,1),
              opacity .3s .25s ease-out;
  transition: transform 4s .25s cubic-bezier(0,1,.3,1),
              opacity .3s .25s ease-out,
              -webkit-transform 4s .25s cubic-bezier(0,1,.3,1);

  will-change: transform, opacity;
}

.show-on-scroll.is-visible {
  opacity: 1;
  -webkit-transform: translateY(0em) !important;
  transform: translateY(0em) !important;
}

.header-btn.show-on-scroll.is-visible {
  transition-delay: 1s;
}


