.down1 {
  animation: 5s ease-out infinite bounce;
}

@keyframes bounce {
  0%, 20%, 53%, 80%, 100% {
      -webkit-transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
      transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0);
  }

  40%, 43% {
      -webkit-transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      -webkit-transform: translate3d(0, -30px, 0);
      transform: translate3d(0, -30px, 0);
  }

  70% {
      -webkit-transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      -webkit-transform: translate3d(0, -15px, 0);
      transform: translate3d(0, -15px, 0);
  }

  90% {
      -webkit-transform: translate3d(0,-5px,0);
      transform: translate3d(0,-5px,0);
  }
}

.down2 {
  animation: 2s ease-out 1 mover forwards;
}

@keyframes mover {
  from {
    transform: rotateZ(0deg);
    right: 30px;
  }
  to {
    transform: rotateZ(-180deg);
    right: 48%;
  }
}

.down3 {
  right: 48% !important;
  animation: 5s ease-out infinite bounce2;
}

@keyframes bounce2 {
  0%, 20%, 53%, 80%, 100% {
      -webkit-transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
      transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
      -webkit-transform: translate3d(0,0,0);
      transform: translate3d(0,0,0) rotateZ(-180deg);
  }

  40%, 43% {
      -webkit-transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      -webkit-transform: translate3d(0, -30px, 0);
      transform: translate3d(0, -30px, 0) rotateZ(-180deg);
  }

  70% {
      -webkit-transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
      -webkit-transform: translate3d(0, -15px, 0);
      transform: translate3d(0, -15px, 0) rotateZ(-180deg);
  }

  90% {
      -webkit-transform: translate3d(0,-5px,0);
      transform: translate3d(0,-5px,0) rotateZ(-180deg);
  }
}

